.App {
  text-align: center;
}

.App-leed {
  -webkit-animation: App-leed-spin infinite 20s linear;
          animation: App-leed-spin infinite 20s linear;
  height: 80px;
  position: relative;
  z-index: 1;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-leed-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-leed-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.map {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
}


.water-station {
  background-image: url('./icons/solid/tint.svg');
  background-size: cover;
  width: 0.95em;
  height: 0.95em;
  border-radius: 50%;
  cursor: pointer;
}

